                                </tr>

                                <tr v-for="item in filtered_data.filter(obj => obj.salary_type == 'Daily')" :key="item.emp_id">
                                    <td> {{ item.employee_full_name }} </td>
                                    <td :class="{'text-danger': item.daily_salary < 0}"> {{ (item.daily_salary).toLocaleString() }}</td>
                                    <td :class="{'text-danger': item.count_present < 0}"> {{ item.count_present }} </td>
                                    <td :class="{'text-danger': item.total_hour < 0}"> {{ item.total_hour }} </td>
                                    <td :class="{'text-danger': ((item.daily_salary * item.count_present) ) < 0}"> {{ (item.daily_salary * item.count_present ).toLocaleString() }}</td>
                                    <td :class="{'text-danger': ((item.daily_salary / 8 ) * item.total_hour) < 0}"> {{ ((item.daily_salary / 8 ) * item.total_hour).toLocaleString() }}</td>
                                    <td :class="{'text-danger': item.food_money < 0}"> {{ (item.food_money).toLocaleString() }}</td>
                                    <td :class="{'text-danger': item.transport_money < 0}"> {{ (item.transport_money).toLocaleString() }}</td>
                                    <td :class="{'text-danger': item.cabina_money < 0}"> {{ (item.cabina_money).toLocaleString() }}</td>
                                    <td :class="{'text-danger': item.expense_money < 0}"> {{ (item.expense_money).toLocaleString() }}</td>
                                    <td :class="{'text-danger': (item.fine_money - item.total_f) < 0}"> {{ ((item.fine_money - item.total_f)).toLocaleString() }}</td>
                                    <td :class="{'text-danger': item.loan_money < 0}"> {{ (item.loan_money).toLocaleString() }}</td>
                                    <td :class="{'text-danger': item.accomodation_money < 0}"> {{ (item.accomodation_money).toLocaleString() }}</td>
                                    <td :class="{'text-danger': item.other_expense < 0}"> {{ (item.other_expense).toLocaleString() }}</td>
