                                    <td :class="{'text-danger': (((item.daily_salary * item.count_present)  + ((item.daily_salary / 8 ) * item.total_hour)  + (item.food_money) - item.total_f + item.transport_money + item.cabina_money + item.expense_money + item.fine_money + item.loan_money + item.accomodation_money + item.other_expense + item.other_minus) ) < 0}">
                                        {{ Math.round((Math.round(item.daily_salary * item.count_present)  + Math.round((item.daily_salary / 8 ) * item.total_hour)  + (item.food_money) - item.total_f + item.transport_money + item.cabina_money + item.expense_money + item.fine_money + item.loan_money + item.accomodation_money + item.other_expense + item.other_minus) ).toLocaleString() }}
                                        {{ item.salary_type == 'Monthly' ? '$' : '' }}
                                    </td>
